  justify-content: center;
  align-items: center;
  height: 50px;
}

.navigation__list {
  display: flex;
  color: #f0f0f0;
}

.navigation__list .svg-icon {
  margin-right: 8px;
}

.navigation__link {
  display: inline-flex;
  align-items: center;
}

.navigation__link:last-child {
  margin-right: 35px;
}

.sidebar {
  position: absolute;
  top: 125px;
  height: 100%;
  z-index: 30;
  background-color: #212020;
  color: #bfbfbf;
  font-family: Verdana;
  font-size: 14px;
  line-height: 1rem;
  transition: all 0.5s;
}

.sidebar--filter {
  left: 0;
  width: 320px;
  border-right: 1px solid #1b1a1a;
}

.sidebar--filter--hidden {
  left: -320px;
}

.sidebar__btn {
  width: 36px;
  height: 44px;
  border-radius: 3px;
  background-color: #212020;
  border: 1px solid #1b1a1a;
  position: absolute;
  top: 22px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  cursor: pointer;
}

.sidebar__btn--filter {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: none;
  right: -36px;
}

.sidebar__btn--filter span {
  display: inline-block;
  height: 2px;
  background-color: #f9f9f9;
}

.sidebar__btn--filter span:not(:last-of-type) {
  margin-bottom: 4px;
}

.sidebar__btn--filter span:nth-of-type(1) {
  width: 16px;
  margin-right: 14px;
}

.sidebar__btn--filter span:nth-of-type(2) {
  width: 8px;
  margin-right: 18px;
}

.sidebar__btn--filter span:nth-of-type(3) {
  width: 4px;
  margin-right: 20px;
}

.sidebar__title {
  font-size: 16px;
  font-weight: 700;
  padding: 38px 0 20px 25px;
  border-bottom: 1px solid #1b1a1a;
}

.fake-checkbox {
  display: inline-block;
  width: 10px;
  height: 10px;
  border: 1px solid #f9f9f9;
  border-radius: 5px;
  position: relative;
  margin-right: 5px;
}

.fake-checkbox::before {
  content: "";
  position: absolute;
  display: block;
  top: 1px;
  left: 1px;
  width: 6px;
  height: 6px;
  background-color: #f9f9f9;
  border-radius: 3px;
  opacity: 0;
  transition: opacity 0.2s;
}

.filter {
  font-size: 14px;
  font-weight: 400;
}

.filter label {
  cursor: pointer;
}

.filter__icon {
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.2s;
}

.filter__icon--arrow {
  width: 10px;
  height: 9px;
  background-image: url(../img/filter/filter__arrow.svg);
  right: -15px;
}

.filter__item--disabled {
  color: #807979;
}

.filter__item--disabled label {
  cursor: default;
}

.filter__item--disabled .fake-checkbox {
  border-color: #807979;
}

.filter__checkbox {
  display: none;
}

.filter__checkbox:checked + .fake-checkbox::before {
  opacity: 1;
}

.filter__checkbox:checked,
.filter__checkbox:checked ~ span {
  color: #f9f9f9;
  transition: color 0.2s;
}

.filter__name {
  display: inline-block;
  padding: 13px 0;
  font-weight: 700;
  width: calc(100% - 25px);
  position: relative;
  transition: color .2s;
}

.filter__name--active {
  color: #f9f9f9;
}

.filter__name--active .filter__icon_arrow {
  transform: translateY(-50%) rotate(180deg);
}

.filter__items-wrapper {
  max-height: 0;
  overflow: hidden;
  transition: all .5s;
}

.filter__items-wrapper .filter__items-list {
  padding-left: 15px;
}

.filter__items-list {
  padding: 18px 0;
}

.filter__item:not(:last-of-type) {
  margin-bottom: 18px;
}

.sidebar-list {
  height: calc(100% - 74px);
  overflow-y: auto;
  padding-left: 10px;
  padding-right: 5px;
}

.sidebar-list__item {
  padding-left: 15px;
  border-bottom: 1px solid #1b1a1a;
}

.sidebar--assemblage {
  background-color: #212020;
  right: 0;
  width: 360px;
  border-left: 1px solid #1b1a1a;
}

.sidebar--assemblage--hidden {
  right: -360px;
}

.sidebar__btn--assemblage {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: none;
  left: -36px;
}

.sidebar__pc-parts {
  margin-top: 10px;
}

.pc-parts {
  padding: 0 30px;
}

.pc-parts__item {
  padding-top: 10px;
  padding-bottom: 15px;
  font-size: 12px;
  border-bottom: 1px solid #1b1a1a;
}

.pc-parts__item .pc-part--multiple:not(:first-child) {
  margin-top: 15px;
}

.pc-parts__add-item {
  margin-top: 12px;
  color: #bfbfbf;
}

.pc-parts__add-item:before {
  content: '+ ';
  font-size: 18px;
  font-weight: 700;
}

.pc-parts__choose-item {
  color: #bfbfbf;
  font-size: 14px;
  font-weight: 700;
}

.pc-parts__choose-item:before {
  content: '+ ';
  font-size: 18px;
  font-weight: 700;
}

.pc-parts__footer {
  display: flex;
  justify-content: space-between;
}

.pc-parts__assemblage-number {
  font-size: 14px;
  color: #807979;
}

.pc-parts__total {
  font-size: 12px;
  color: #afafaf;
}

.pc-part {
  display: flex;
}

.pc-part--current {
  font-size: 14px;
  color: #807979;
}

.pc-part__description {
  margin-right: auto;
}

.pc-part__title {
  color: #da0b29;
}

.pc-part__details {
  color: #807979;
}

.pc-part__price {
  color: #bfbfbf;
  font-size: 11px;
  position: relative;
}

.pc-part__price:after {
  content: '₽';
  position: absolute;
  right: -10px;
}

.pc-part:hover .pc-part__price {
  color: #f9f9f9;
}

.conflicts__title {
  width: 320px;
  height: 35px;
  margin-bottom: 20px;
  padding-left: 15px;
  line-height: 35px;
  font-size: 16px;
  font-weight: 700;
  color: #212020;
  background-color: #e0a006;
  border-radius: 3px;
}

.conflicts__item {
  margin-left: 12px;
  font-size: 14px;
  color: #e0a006;
}

.conflicts__item:not(:last-child) {
  margin-bottom: 15px;
}

.conflicts__item:before {
  content: '! ';
  font-size: 16px;
  font-weight: 700;
}

.container {
  width: 1060px;
  margin: 24px auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: Verdana, sans-serif;
