}

.product_long {
  width: 100%;
  height: 250px;
}

.product_long .product__wrapper {
  display: flex;
}

.product_long .product__main-part {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  justify-content: space-between;
}

.product_long .product__title {
  order: 1;
  margin: 0;
}

.product_long .product__hidden-description {
  order: 2;
  display: block;
  padding: 0;
  margin: 0;
  align-self: center;
  border: none;
}

.product_long .product__price-and-buttons {
  order: 3;
  margin: 0;
}

.product_long .product__image-wrapper {
  flex-shrink: 0;
}

.product__link {
  cursor: pointer;
}

.product__image-wrapper {
  width: 465px;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #f9f9f9;
  border-radius: 10px;
}

.product__image-wrapper:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
  background-image: radial-gradient(ellipse 190px 110px, transparent, 80%, #1b1a1a);
}

.product__image {
  max-width: 100%;
  max-height: 100%;
}

.product__main-part {
  padding: 6px 20px 20px;
}

.product__title {
  font-size: 16px;
  color: #f9f9f9;
}

.product__price-and-buttons {
