.product-card .product_long {
  height: auto;
}

.product-card .product__wrapper {
  flex-wrap: wrap;
}

.product-card .product__title {
  order: 1;
  padding: 17px 17px 20px;
}

.product-card .product__photos {
  order: 2;
}

.product-card .product__image-wrapper {
  width: 475px;
  height: 267px;
}

.product-card .product__main-part {
  order: 3;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
}

.product-card .product__price-and-buttons {
  order: 1;
}

.product-card .product__price {
  margin-left: 0;
}

.product-card .product__hidden-description {
  margin: 35px 0 25px;
}

.product-card .product__rating {
  order: 3;
}

.product-slider {
  overflow: hidden;
  height: 56px;
  width: 475px;
  position: relative;
}

.product-slider__track {
  display: flex;
  transition: transform 1s;
}

.product-slider__item-wrapper:not(:last-child) {
  margin-right: 1px;
}

.product-slider__item {
  max-height: 56px;
}

.product-slider__button {
  position: absolute;
  top: 0;
  height: 56px;
  width: 30px;
  cursor: pointer;
  transition: opacity .5s;
}

.product-slider__button_next {
  right: 0;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #1b1a1a 60%, #1b1a1a 100%);
}

.product-slider__button_prev {
  left: 0;
  opacity: 0;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, #1b1a1a 60%, #1b1a1a 100%);
}

.product-score {
  display: flex;
  align-items: center;
  height: 30px;
  font-size: 14px;
  color: #f9f9f9;
}

.product-score__rating {
  margin-right: 10px;
  font-weight: 700;
}

.product-score__year-rate:not(:last-child) {
  margin-right: 10px;
}

.percent-circle {
  position: absolute;
  top: 0;
  left: 0;
}

.percent-circle__circle {
  transform-origin: center;
  transform: rotate(-90deg);
}

.year-rate {
  display: inline-flex;
  height: 30px;
  align-items: center;
  font-weight: 400;
  padding-left: 5px;
  border-left: 1px solid #212020;
}

.year-rate__percentage {
  margin: 0 12px;
  display: inline-flex;
  height: 30px;
  align-items: center;
}

.year-rate__value {
  position: relative;
  font-size: 11px;
  margin-right: 5px;
  display: inline-flex;
  height: 30px;
  width: 30px;
  line-height: 30px;
  justify-content: center;
}

.year-rate__year {
  display: inline-block;
  width: 60px;
  height: 18px;
  border-radius: 2px;
  text-align: center;
  padding: 2px 0;
  background-color: #f9f9f9;
  color: #1b1a1a;
}

.product-rating {
  width: 330px;
  min-height: 49px;
  text-align: center;
}

.product-rating__value {
  text-align: center;
}

.product-rating__average {
  display: inline-block;
  margin-bottom: 5px;
  font-size: 18px;
  color: #da0b29;
}

.product-rating__max {
  font-size: 14px;
  color: #807979;
  font-weight: 400;
}

.product-rating__stars {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.product-rating__stars[data-total-value="1"] .product-rating__star:nth-child(n + 11),
.product-rating__stars[data-total-value="2"] .product-rating__star:nth-child(n + 10),
.product-rating__stars[data-total-value="3"] .product-rating__star:nth-child(n + 9),
.product-rating__stars[data-total-value="4"] .product-rating__star:nth-child(n + 8),
.product-rating__stars[data-total-value="5"] .product-rating__star:nth-child(n + 7),
.product-rating__stars[data-total-value="6"] .product-rating__star:nth-child(n + 6),
.product-rating__stars[data-total-value="7"] .product-rating__star:nth-child(n + 5),
.product-rating__stars[data-total-value="8"] .product-rating__star:nth-child(n + 4),
.product-rating__stars[data-total-value="9"] .product-rating__star:nth-child(n + 3),
.product-rating__stars[data-total-value="10"] .product-rating__star:nth-child(n + 2) {
  color: #da0b29;
}

