.container {
	width: 1060px;
	margin: 24px auto 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: Verdana, sans-serif;
	font-weight: 700;
}

.content-management {
	width: 100%;
	display: flex;
	align-items: center;
	margin-bottom: 20px;

	&__button {
		background-color: #212020;
		border-radius: 3px;
		border: 1px solid #1b1a1a;

		&--back {
			height: 40px;
			padding-left: 13px;
			padding-right: 24px;
			color: #979797;
			font-weight: 700;
		}

		&--search {
			width: 40px;
			height: 40px;
			margin-left: auto;
			margin-right: 11px;
		}

		&--view-type {
			width: 108px;
			height: 40px;

			&--one-in-row {
			}

			&--two-in-row {
			}

			&--three-in-row {
			}
		}
	}

	&__icon {
		&_back-arrow {
		}

		&_search {
		}
	}
}

.main {
	width: 950px;

	&__products {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__product {
		margin-bottom: 20px;
	}
}

.product {
	height: 350px;
	width: 465px;
	&__wrapper {
		width: 100%;
		overflow: hidden;
		background-color: #1b1a1a;
		border-radius: 3px;
		box-shadow: 0px 0px 4.5px 0.5px rgba(0, 0, 0, 0.2);
	}

	&:hover {
		.product__wrapper {
			position: relative;
			z-index: 20;
			box-shadow: 0px 0px 13.5px 1.5px rgba(0, 0, 0, 0.5);
		}

		.product__hidden-description {
			display: block;
		}
	}

	&_long {
		width: 100%;
		height: 250px;

		.product {
			&__wrapper {
				display: flex;
			}

			&__main-part {
				display: flex;
				flex-direction: column;
				padding-top: 20px;
				justify-content: space-between;
			}

			&__title {
				order: 1;
				margin: 0;
			}

			&__hidden-description {
				order: 2;
				display: block;
				padding: 0;
				margin: 0;
				align-self: center;
				border: none;
			}

			&__price-and-buttons {
				order: 3;
				margin: 0;
			}

			&__image-wrapper {
				flex-shrink: 0;
			}
		}
	}

	&__link {
		cursor: pointer;
	}

	&__image-wrapper {
		width: 465px;
		height: 250px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		background-color: #f9f9f9;
		border-radius: 10px; //костыль

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			display: inline-block;
			background-image: radial-gradient(ellipse 190px 110px, transparent, 80%, #1b1a1a);
		}
	}

	&__image {
		max-width: 100%;
		max-height: 100%;
	}

	&__main-part {
		padding: 6px 20px 20px;
	}

	&__title {
		font-size: 16px;
		color: #f9f9f9;
	}

	&__price-and-buttons {
		display: flex;
		align-items: center;
		margin-top: 17px;
	}

	&__rating {
		font-size: 14px;
		color: #f9f9f9;
	}

	&__price {
		align-self: flex-end;
		margin-left: auto;
		margin-right: 14px;
		padding: 0 6px 6px 6px;
		border-bottom: 1px solid #da0b29;
		font-size: 14px;
		font-weight: 400;
		color: #7c7575;
	}

	&__price-value {
		font-weight: 700;
		color: #f9f9f9;
	}

	&__button {
		border-radius: 3px;

		&_compare {
			width: 25px;
			height: 25px;
			background-color: #bfbfbf;
			margin-right: 10px;
		}

		&_to-build {
			width: 110px;
			height: 25px;
			background-color: #da0b29;
			font-weight: 700;
			color: #1b1a1a;
		}
	}

	&__icon {
		&_compare {
		}

		&_to-build {
		}
	}

	&__hidden-description {
		display: none;
	}
}

.product-info {
	margin-top: 20px;
	padding-top: 20px;
	border-top: 1px solid #212020;

	&__field {
		font-size: 14px;

		&:not(:last-child) {
			margin-bottom: 13px;
		}
	}

	&__title {
		color: #807979;
		font-weight: 400;
	}

	&__value {
		color: #bfbfbf;
	}
}
